*{
  box-sizing: border-box;
  margin: 0; 
  padding: 0;
}
body{
  font-family: 'Courier New', Courier, monospace;
}
.App{
  background: rgb(180, 241, 241);
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}


h1{
  color: rgb(53, 131, 131);
  font-size: 30px;
  font-weight: 600;
  padding: 50px;
}
.flex{
  display: flex;
  gap: 20px;
}
.search{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image:url('data:image/jpeg;base64,') ;
  background-size: cover;
  background-repeat: no-repeat;
  height: 450px;
}
.search input{
  padding: 12px;
  outline: none;
  border: none;
  border-radius: 15px;
  font-size: 16px;
  width: 50%;
}
.container{
  flex: 1;
}
.location-box .location h3{
  font-size: 26px;
  color: rgb(53, 131, 131);
  padding-bottom: 12px;
}
.location-box .date{
  font-size: 16px;
  color: rgb(41, 165, 241);
  padding-bottom: 14px;
}
.location-box p{
  font-size: 16px;
  color: rgb(41, 165, 241);
}
.temp{
width: 450px;
height: 200px;
display: flex;
justify-content: space-between;
align-items: center;
}
.temp img{
  width: 180x;
  height: 180px;
}
.temp span{
  font-size: 22px;
  color: rgb(53, 131, 131);
}

.weather-box{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.box{
  width: 180px;
  height: 100px;
  background: white;
  border-radius: 10px;
}
.icon{
  font-size: 50px;
  color: rgb(41, 165, 241);
  padding: 12px;
}
.box h3{
  font-size: 20px;
  padding: 6px;
  color: rgb(53, 131, 131);
}
.box h3 span{
  font-size: 16px;
  color: rgb(53, 131, 131);
  
}